<template>

    <form class="credit-selection-form"
          :class="{'disabled': disabled}"
          @submit.prevent="routeCredit">

        <div class="credit-selection-form-row">

            <div class="credit-selection-form-col">

                <div class="form-group-2 with-icon">
                    <label class="form-label-2">Сумма кредита, руб</label>
                    <div class="range-slider-group">
                        <span class="range-slider-fill">
                            <span :style="{width: this.disabled ? 0 : `${100 * sum / 100000000}%`}"/>
                        </span>
                        <input type="tel"
                               class="form-control no-label"
                               v-mask="decimalMask"
                               v-model="sum"
                               @focus="focusCreditSum"
                               @blur="blurCreditSum">
                        <vue-slider v-if="!this.disabled"
                                    v-model="sum"
                                    :min="this.disabled ? 0 : creditSum.min"
                                    :max="creditSum.max"/>
                    </div>
                    <span class="form-group-icon">
                        <span class="icon-ruble"></span>
                    </span>
                </div>

            </div>

            <div class="credit-selection-form-col">
                <div class="form-group-2">
                    <label class="form-label-2">Срок</label>
                    <div class="range-slider-group">
                        <span class="range-slider-fill">
                            <span :style="{width: this.disabled ? 0 : `${100 * (creditTerm.value-creditTerm.min) / (creditTerm.max - creditTerm.min)}%`}" />
                        </span>
                        <label class="range-slider-label">
                            <div v-if="rangeFocus"
                                 class="range-slider-input">
                                <input type="tel"
                                       class="form-control no-label"
                                       v-mask="decimalMask"
                                       :style="{'width' : `${hiddenTextWidth}px`}"
                                       @blur="termMinMaxCheck"
                                       v-model="term">
                                <span ref="inputHiddenText" class="hidden-text"><span>{{term}}</span></span>
                                <span class="range-slider-input-text">{{parseDate(term)}}</span>
                            </div>
                            <div v-else
                                 class="range-slider-nofocus-text"
                                 @click="rangeFocus = true">{{termModify}}
                            </div>
                        </label>
                        <vue-slider v-if="!this.disabled"
                                    v-model="term"
                                    :min="this.disabled ? 0 : creditTerm.min"
                                    :max="creditTerm.max"/>
                    </div>
                </div>
            </div>

            <div class="credit-selection-form-col wide">

                <div class="credit-selection-target">
                    <div class="form-group-2">
                        <label class="form-label-2">Цель кредита</label>
                        <v-select :value="creditTarget"
                                  :options="creditTargetOptions"
                                  :searchable="false"
                                  @option:selecting="updateCreditTarget"/>
                    </div>
                </div>

            </div>

        </div>

        <div v-if="creditInfo"
             class="credit-selection-info">
            <div class="credit-selection-info-item">Ставка: <span>{{creditInfo.rate}}%</span></div>
            <div class="credit-selection-info-item">Комиссия: <span>{{creditInfo.commission}} руб.</span></div>
        </div>

        <div class="credit-selection-footer">

            <div class="credit-selection-footer-credit-line">

                <FormGroupSwitchCheckbox v-model="creditLine"
                                         @change="changeCreditLine">
                    Кредитная линия
                </FormGroupSwitchCheckbox>

            </div>

            <div class="credit-selection-footer-insure-life">

                <FormGroupSwitchCheckbox v-model="creditInsureLife"
                                         @change="changeInsure">
                    Застраховать жизнь директора<br><span class="green">Вероятность одобрения +30% Снижение ставки -1,5%</span>
                </FormGroupSwitchCheckbox>

            </div>

            <div class="form-submit">
                <button v-if="!disabled && orderStep1"
                        type="submit"
                        class="btn"
                        :disabled="load"
                        :class="{'loading': load}">Продолжить</button>
            </div>

        </div>

    </form>

</template>

<script>

    import createNumberMask from 'text-mask-addons/dist/createNumberMask';
    import VueSlider from 'vue-slider-component'
    import {mapState} from 'vuex'
    import 'vue-slider-component/theme/default.css'
    import FormGroupSwitchCheckbox from "@/components/Inputs/FormGroupSwitchCheckbox";

    const currencyMask = createNumberMask({
        prefix: '',
        allowDecimal: true,
        includeThousandsSeparator: true,
        allowNegative: false,
        thousandsSeparatorSymbol: ' '
    });

    export default {
        name: 'Home',
        props: {
            orderStep1: Boolean,
            disabled: {
                type: Boolean,
                default: false
            },
            creditInfo: Object
        },
        components: {
            FormGroupSwitchCheckbox,
            VueSlider,
        },
        data: () => ({
            decimalMask: currencyMask,
            creditTargetOptions: [
                'Финансирование текущей деятельности',
                'Финансирование кассовых разрывов',
                'Финансирование исполнения контракта',
                'Инвестиции в основные средства'
            ],
            orderId: '',
            hiddenTextWidth: 0,
            rangeFocus: false,
            load: false
        }),
        computed: {
            ...mapState({
                creditSum: state => state.createOrder.creditSum,
                creditTarget: state => state.createOrder.creditTarget,
                creditTerm: state => state.createOrder.creditTerm,
            }),
            creditInsureLife: {
                get() {
                    return this.$store.state.createOrder.creditInsureLife
                },
                set(value) {
                    this.$store.commit('updateCreditInsureLife', value)
                }
            },
            creditLine: {
                get() {
                    return this.$store.state.createOrder.creditLine
                },
                set(value) {
                    this.$store.commit('updateCreditLine', value)
                }
            },
            sum: {
                get() {
                    return this.disabled ? 0 : parseInt(this.creditSum.value.toString().replace(/\s/g, ''))
                },
                set(value) {
                    this.$store.commit('setCreditSum', value)
                }
            },
            term: {
                get() {
                    let val = parseInt(this.creditTerm.value.toString().replace(/\s/g, ''))
                    return this.disabled || isNaN(val) ? 0 : val
                },
                set(value) {
                    this.$store.commit('setCreditTerm', value)
                }
            },
            termModify() {
                let term = parseInt(this.term.toString().replace(/\s/g, ''))
                let text = ''
                if ( this.term < 12 ) {
                    text = `${term} ${this.$options.filters.declensionNumbers(term, ['месяц', 'месяца', 'месяцев'])}`
                }
                else {
                    let year = parseInt(term / 12),
                        months = term % 12,
                        yearText = `${year} ${this.$options.filters.declensionNumbers(year, ['год', 'года', 'лет'])}`,
                        monthsText = months !== 0 ? ` ${months} ${this.$options.filters.declensionNumbers(months, ['месяц', 'месяца', 'месяцев'])}` : '';
                    text = yearText + monthsText
                }
                return text
            },
        },
        methods: {
            routeCredit() {
                this.load = true
                if (!this.disabled) {
                    this.$store.dispatch('updateOrder', this.orderId).then(() => {
                        this.$store.dispatch('nextStep').then(() => {
                            this.load = false
                        }).catch(() => {
                            this.load = false
                        })
                    }).catch(() => {
                        this.load = false
                    })
                }
            },
            focusCreditSum(e) {
                let val = e.target.value
                if ( val === '0' ) {
                    e.target.value = ''
                }
            },
            blurCreditSum(e) {
                let val = e.target.value.length > 0 ? e.target.value : '0'
                e.target.value = val
                let sum = parseInt(val.toString().replace(/\s/g, '')),
                    min = this.creditSum.min,
                    max = this.creditSum.max;
                if ( sum > max ) {
                    sum = max
                }
                if ( sum < min ) {
                    sum = min
                }
                this.$store.commit('setCreditSum', sum)
            },
            updateCreditTarget(e) {
                this.$store.commit('setCreditTarget', e)
            },
            changeInsure(val) {
                this.$store.commit('setCreditInsureLife', val)
            },
            changeCreditLine(val) {
                this.$store.commit('setCreditLine', val)
            },
            parseDate(date) {
                return this.$options.filters.declensionNumbers(parseInt(date.toString().replace(/\s/g, '')), ['месяц', 'месяца', 'месяцев'])
            },
            termMinMaxCheck() {
                let term = parseInt(this.term.toString().replace(/\s/g, '')),
                    min = this.creditTerm.min,
                    max = this.creditTerm.max;
                if ( term > max ) {
                    term = max
                }
                if ( term < min ) {
                    term = min
                }
                this.rangeFocus = false
                this.term = term
            },

        },
        watch: {
            term() {
                setTimeout(() => {
                    this.hiddenTextWidth = this.$refs.inputHiddenText ? this.$refs.inputHiddenText.offsetWidth : 0
                }, 100)
            }
        },
        mounted() {
            this.orderId = parseInt(this.$route.params.id)
            this.hiddenTextWidth = this.$refs.inputHiddenText ? this.$refs.inputHiddenText.offsetWidth : 0
        }
    }
</script>
